<template>
  <div class="content">
    <div class="collapse">
      <h1 style="padding: 25px 0 0 0">Student Profile</h1>

      <el-input
        id="name-input"
        placeholder="Search by name"
        prefix-icon="el-icon-search"
        v-model="inputName">
      </el-input>

      <el-input
        id="tag-input"
        placeholder="Search by tag"
        prefix-icon="el-icon-search"
        v-model="inputTag">
      </el-input>

      <el-collapse v-model="activeStudents" @change="handleChange" class="el-collapse">
        <el-collapse-item v-for="item in students" :key="item.id" :name="item.id">
          <template slot="title">
            <div class="avatar">
              <div class="block"><el-avatar :size="100" :src="item.pic"></el-avatar></div>
            </div>
            <div class="info">
              <h1>{{item.firstName}} {{item.lastName}}</h1>
              <p>Email: {{item.email}}</p>
              <p>Company: {{item.company}}</p>
              <p>Average: {{item.average}}%</p>
            </div>
          </template>

          <div class="grades">
            <p v-for="(g,index) in item.grades" :key="index">Test{{index}}:<i style="margin-left: 20px"></i>{{g}}%</p>
            <el-tag
              :key="item.id+tag"
              v-for="tag in item.tags"
              :disable-transitions="false">
              {{tag}}
            </el-tag>

            <el-input
              class="input-new-tag add-tag-input"
              v-model="inputValue"
              ref="saveTagInput"
              size="small"
              @keyup.enter.native="handleNewTag(item.id)"
              @blur="handleNewTag">
            </el-input>
          </div>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>



<script>
export default {
  name: 'Profile',
  data() {
      return {
        allStudents: [],
        activeStudents: [],
        inputName: '',
        inputTag: '',
        inputValue: ''}
  },

  mounted() {
    this.$http.get('https://www.hatchways.io/api/assessment/students').then((res) =>
    {
      this.allStudents = res.data.students;
      this.allStudents.map(x => {x.tags=[]; x.average = this.getAverage(x.grades)})
    }).catch((err) => {
      console.log(err)
    })
  },

  computed:{
    students: function(){
      let reg = new RegExp(this.inputName,'i');
      let reg2 = new RegExp(this.inputTag,'i');
      let temp = this.allStudents.filter((item) => {return (item.firstName + item.lastName).match(reg)!= null});
      if (this.inputTag !== '')
      {
        return temp.filter((item) => {
          return item.tags.filter((tag) => {
            return tag.match(reg2) != null}).length!== 0})
      }
      else {
        return temp
      }
    }
  },

  methods: {
    getAverage(array)
    {
      let sum = 0;
      array.map(x => sum += Number(x));
      return sum / array.length
    },

    handleChange(val)
    {
      console.log(val);
    },

    handleNewTag(index)
    {
      let i = Number(index)-1;
      let inputValue = this.inputValue;
      if (inputValue)
      {
        this.students[i]['tags'].push(inputValue);
      }
      this.inputValue = '';
    }
  }
}
</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.content
{
  padding: 50px 250px;
  border-radius: 50px;
}

input.el-input__inner
{
    border: unset;
    border-bottom: solid 1px;
    width: 99%;
    border-radius: unset;
}

.collapse
{
  width: 100%;
  border-radius: 25px;
  background-color: white;
}

.el-collapse
{
  padding: 20px;
  border:unset!important;
}

.el-collapse-item__header
{
  height: auto!important;
  line-height: unset!important;
}

.info
{
  text-align: left;
  margin-left: 50px;
}

.grades
{
  text-align: left;
  padding: 0 150px;
}
</style>